import React from 'react';
import { Nav, Navbar } from "react-bootstrap";
import { withRouter } from 'react-router-dom';
import { AiOutlineCloudUpload, AiOutlinePoweroff,AiOutlineDropbox } from 'react-icons/ai';
import { FaGoogleDrive } from "react-icons/fa";
import './SideBar.css';

const SideBar = (props) => {
    console.log(props)
    return (
        <div>
            <Nav
                variant="pills"
                defaultActiveKey={props.location.pathname}
                className="sidebar"
            >
                <Nav.Item style={{ width: "100%" }}>
                    <Nav.Link href="/home/upload" className="navitem">
                        <AiOutlineCloudUpload
                            style={{ height: "20px", width: "20px" }} />
                        <p style={{ marginLeft: "20px" }}>Upload</p>
                    </Nav.Link>
                </Nav.Item>
                <Nav.Item style={{ width: "100%" }}>
                    <Nav.Link href="/home/drive" className="navitem">
                        <FaGoogleDrive
                            style={{ height: "20px", width: "20px" }} />
                        <p style={{ marginLeft: "20px" }}>Drive</p>
                    </Nav.Link>
                </Nav.Item>
                <Nav.Item style={{ width: "100%" }}>
                    <Nav.Link href="/home/dropbox" className="navitem">
                        <AiOutlineDropbox
                            style={{ height: "20px", width: "20px" }} />
                        <p style={{ marginLeft: "20px" }}>DropBox</p>
                    </Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link href="/" className="navitem">
                        <AiOutlinePoweroff
                            style={{ height: "20px", width: "20px" }} />
                        <p style={{ marginLeft: "20px" }}>Logout</p>
                    </Nav.Link>
                </Nav.Item>
            </Nav>
        </div>
    )
}

export default withRouter(SideBar);
