import React, { Component } from 'react';
import { Container, Row, Col } from "react-bootstrap";
//  , Card, Form, Button
import Sidebar from '../../components/SideBar/SideBar';
import { Form, Button, ProgressBar } from 'react-bootstrap';
import './Home.css';

class Home extends Component {
    constructor(props) {
        super(props)

        this.state = {
            files:null,
        }
    }

    componentDidMount = () => {
        console.log(this.props);
        console.log(this.props.location.search);
        fetch(`http://localhost:8080/google/callback${this.props.location.search}`)
                .then(response => 
                    response.json())
                .then(json => {
                    console.log('now can upload')
                    console.log(json);
                })
                .catch(err => console.log(err));
    }
    createFormData = (file, body,type) => {
        const data = new FormData();
      
        data.append(type,file);

        console.log(data,file)
        return data;
      };
    
      handleUploadPhoto = () => {
          fetch("http://localhost:8080/upload", {
            method: "POST",
            body: this.createFormData(this.state.files[0], {},'image')
          })
            .then(response => {
                console.log(response)
              //console.log("upload  this.setState({ photo: null });succes", response);
              alert("Upload success!");

            })
            .catch(error => {
              console.log("upload error", error);
              alert("Upload failed!");
            });
        };

        handleChange = (files) => {
            console.log(files)
            this.setState({
                files: files
            });
        }
        

    render() {
        return (
            <div className="rowfluid">
                <div className="columns">
                    <Sidebar className="sidebar" />
                </div>
                <div className="rightDiv">
                    <Form>
                        <Form.Group>
                            <Form.File id="exampleFormControlFile1" label="Example file input" onChange={(e) => this.handleChange(e.target.files)} />
                        </Form.Group>
                        <Button variant="primary" onClick={this.handleUploadPhoto}>
                            Upload
                        </Button>
                    </Form>
                </div>

            </div>

        )
    }
}

export default Home