import React, { Component } from 'react';
import Sidebar from '../../components/SideBar/SideBar';
import './Upload.css';
import { Form, Button, ProgressBar } from 'react-bootstrap';
import firebase from '../../server/firebase';

class Upload extends Component {
    constructor(props) {
        super(props)

        this.state = {
            titleCourse: "",
            files: null,
            progress: 0.0
        }
    }

    inputHandler = (event) => {
        this.setState({ [event.target.name]: event.target.value });
    }

    handleChange = (files) => {
        this.setState({
            files: files
        });
    }
    saveHandler = () => {
        let bucketName = 'courses';
        let file = this.state.files[0];
        let storageRef = firebase.storage().ref(`${bucketName}/${this.state.titleCourse}/${file.name}`);
        let uploadTask = storageRef.put(file);
        uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
            (snapshot) => {
                var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                this.setState({ progress: progress });
                console.log('Upload is ' + progress + '% done');
                switch (snapshot.state) {
                    case firebase.storage.TaskState.PAUSED: // or 'paused'
                        console.log('Upload is paused');
                        break;
                    case firebase.storage.TaskState.RUNNING: // or 'running'
                        console.log('Upload is running');
                        break;
                }
            }, (error) => {
                console.log(error);
            }, () => {
                uploadTask.snapshot.ref.getDownloadURL().then(function (downloadURL) {
                    alert('file uploaded successfully');
                    console.log('File available at', downloadURL);
                });
            }
        )

    }

    render() {
        if (this.state.progress > 0 && this.state.progress < 100) {
            var progress = (
                <ProgressBar style={{ marginTop: "20px" }} now={this.state.progress} label={`${this.state.progress}%`} />
            );
        } else{
            var progress = null;
        }
        return (
            <div className="rowfluid" >
                <div className="columns">
                    <Sidebar className="sidebar" />
                </div>
                <div className="rightDiv">
                    <div className="formGroup">
                        <Form>
                            <Form.Group controlId="formBasicTitle">
                                <Form.Label>Tile of the Course</Form.Label>
                                <Form.Control
                                    type="text"
                                    name="titleCourse"
                                    value={this.state.titleCourse}
                                    onChange={(e) => this.inputHandler(e)}
                                    placeholder="Eg. FIRST STEPS TOWARDS PYTHON" />
                                <Form.Text className="text-muted">
                                    Main title to attract audience
                                </Form.Text>
                            </Form.Group>

                            <Form.Group>
                                <Form.File id="exampleFormControlFile1" label="Example file input" onChange={(e) => this.handleChange(e.target.files)} />
                            </Form.Group>
                            <Button variant="primary" onClick={this.saveHandler}>
                                Save
                            </Button>
                            {progress}
                        </Form>
                    </div>
                </div>
            </div>
        )
    }
}

export default Upload;