import React, { Component } from 'react'
import { AiTwotoneLock } from 'react-icons/ai';
import Sidebar from '../../components/SideBar/SideBar';
import { Button } from 'react-bootstrap';
import { IoLogoGoogleplus } from "react-icons/io";
import './Drive.css';

class Drive extends Component {
    constructor(props) {
        super(props)

        this.state = {
            url:'',
            urlreceived:false
        }
    }

    componentDidMount = async () => {
        var url = await this.urlHandler();
        console.log(url);
        this.setState({url:url});
        this.setState({urlreceived:true});
        // this.props.history.push({pathname:`/${url}`});
    }

    urlHandler = () => {
        return new Promise((res, rej) => {
            fetch('http://localhost:8080/home/drive')
                .then(response => 
                    response.json())
                .then(json => {
                    console.log(json);
                    //var msg = JSON.parse(json);
                    //console.log(msg);
                    console.log(json.url);
                    console.log('Server recieved');
                    res(json.url);
                    // console.log('msg:',msg);
                    // res(msg.msg);
                })
                .catch(err => console.log(err));
        })

    }

    render() {
        if(this.state.urlreceived){
        var link = (
            <a href={this.state.url}><Button onClick={this.buttonHandler} variant="danger"><IoLogoGoogleplus />Google</Button></a>
        );
        } else{
            var link = (
                <Button onClick={this.buttonHandler} variant="danger"><IoLogoGoogleplus />Google</Button>
            );
        }
        return (
            <div className="rowfluid" >
                <div className="columns">
                    <Sidebar className="sidebar" />
                </div>
                <div className="rightDiv">
                    <div className="firstLine">
                        <h1><AiTwotoneLock />GOOGLE DRIVE UPLOAD</h1>
                    </div>
                    <p>Login or Register with:</p>
                    {link}
                </div>
            </div>
        )
    }
}

export default Drive