import React, { Component, useState } from "react";
import DataRow from './DataRow';

const style = {
  table: {
    borderCollapse: "collapse"
  },
  tableCell: {
    border: "1px solid gray",
    margin: 0,
    padding: "5px 10px",
    width: "max-content",
    minWidth: "150px"
  },
  form: {
    container: {
      padding: "20px",
      border: "1px solid #F0F8FF",
      borderRadius: "15px",
      width: "max-content",
      marginBottom: "40px"
    },
    inputs: {
      marginBottom: "5px"
    },
    submitBtn: {
      marginTop: "10px",
      padding: "10px 15px",
      border: "none",
      backgroundColor: "lightseagreen",
      fontSize: "14px",
      borderRadius: "5px"
    }
  }
};

class PhoneBookForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      fname: "",
      lname: "",
      phone: ""
    };
  }
  handleData = () => {
    console.log("handling")
    return (
      <div>
        <h1>dcdcdc</h1>
        <DataRow
          
        />
      </div>
    );
  };
  handleChange(event) {
    this.setState({ [event.target.name]: event.target.value });
  }
  render() {
    return (
      <form
        onSubmit={(e) => e.preventDefault(this.handleData)}
        style={style.form.container}
      >
        <label>First name:</label>
        <br />
        <input
          style={style.form.inputs}
          className="userFirstname"
          name="fname"
          value={this.state.fname}
          onChange={(e) => this.handleChange(e)}
          type="text"
        />
        <br />
        <label>Last name:</label>
        <br />
        <input
          style={style.form.inputs}
          className="userLastname"
          name="lname"
          value={this.state.lname}
          onChange={(e) => this.handleChange(e)}
          type="text"
        />
        <br />
        <label>Phone:</label>
        <br />
        <input
          style={style.form.inputs}
          className="userPhone"
          name="phone"
          value={this.state.phone}
          onChange={(e) => this.handleChange(e)}
          type="text"
        />
        <br />
        <button
          style={style.form.submitBtn}
          type="submit"
          label="Add User"
          onClick={this.handleData}
        >
          Add User
        </button>
      </form>
    );
  }
}

