class InformationTable extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [
        {
          first:"First name",
          last: "Last name",
          pno: "Phone"
        },
        {
          first:"Amrit",
          last: "Minocha",
          pno: "999999999"
          
        }
        // },
        // {
        //   first:"Am",
        //   last: "mi",
        //   pno: "12"
        // }
      ]
    }
  }
  
  renderOneRow=(row,i)=> {
    console.log("huhuhuhu")
    return <DataRow key={i} />
  }
  renderRows = (returnedRows) => {
    if(returnedRows){
      returnedRows.map((row,i)=>{
        return <DataRow key={i}/>;
        // this.renderOneRow(row,i);
        // console.log(row);
        // return <DataRow first={row} last={row} pno={row} key={i} />
      })
    } else {
      return null;
    }
  }


  render() {
    var rows = [
      <tr>
          <th style={style.tableCell}>First name</th>
          <th style={style.tableCell}>Last name</th>
          <th style={style.tableCell}>Phone</th>
      </tr>
    ];
    for (let i of this.state.data) {
      console.log(i);
      rows.push = (
        <tr>
          <th style={style.tableCell}>xsx</th>
          <th style={style.tableCell}>xxx</th>
          <th style={style.tableCell}>www</th>
        </tr>
      )
    }

    return (
      <table style={style.table} className="informationTable">
        <thead>
          {/* {this.renderRows(this.state.data)} */}
          {
            this.state.data.map((row,i)=>{
              return <DataRow 
                key={i} 
                first={row.first}
                last={row.last}
                pno={row.pno} />
            })
          }
        </thead>
      </table>
    );
  }
}

function Application(props) {
  return (
    <section>
      <PhoneBookForm />
      <InformationTable />
    </section>
  );
}

export default Application;
